@import "../scss/variables";

.header {
  &__desktop, &__banner {
    width: 100%;
  }
  &__backgrounds {
    display: flex;
    width: 100%;
    height: 162px;
  }
  &__inner {
    width: 100%;
    padding: 0 9vw;
    position: absolute;
    top: 50px;
    display: flex;
    flex-direction: row;

    .header__toggle {
      display: flex;
      align-items: center;
      width: 70%;
      margin-right: 100px;

      button {
        position: relative;
        margin: 0 15px;
        height: 20px;
        width: 40px;
        cursor: pointer;
        border: 0;
        border-radius: 10px;
      }
    }
  
  }

  &__slider {
    position: absolute;
    height: 14px; 
    width: 14px;
    top: 3px; 
    left: 4px;
    border: 0;
    border-radius: 50%;
    background-color: $primaryLight;
    transition: .2s;
  }

  &.active {
    .slider {
        transform: translateX(calc(100% + 4px));
    }
  }
}

